      setIsSaving(true);
      const filename = saveFilename || generateDefaultFilename();
      
      // Utiliser le service de s√©curit√© pour la sauvegarde compl√®te
      const backupData = await SecurityService.createCompleteBackup(savePassword, saveDescription);

      // T√©l√©charger le fichier
      const blob = new Blob([backupData], { type: 'application/json' });
      const url = URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.href = url;
      link.download = filename;
      link.click();
      URL.revokeObjectURL(url);

      toast({
        title: "Succ√®s",
        description: "Donn√©es sauvegard√©es avec succ√®s",
        variant: "default"
      });

